/* eslint-disable */
let SEPARATION = 100,
    AMOUNTX = 50,
    AMOUNTY = 50
let container   //idclasshtml
let camera, scene, renderer   // 
let particles,
    particle,
    count = 0
let mouseX = 50,  //
    mouseY = -200
// var windowHalfX = window.innerWidth / 2;
// var windowHalfY = window.innerHeight / 2;

import * as THREE from 'three55'   //npm install three threejsthree.js

function initWave(id) {
    //
    camera = new THREE.PerspectiveCamera(
        100,
        window.innerWidth / window.innerHeight,
        20,
        2000
    )
    camera.position.z = 500
    //
    scene = new THREE.Scene()

    //
    let PI2 = Math.PI * 2
    particles = new Array()
    let material = new THREE.ParticleCanvasMaterial({
        color: 0x75767F,
        program: function (context) {
            context.beginPath()
            context.arc(0, 0, 1, 0, PI2, true)
            context.fill()
        }
    })
    let i = 0
    for (let ix = 0; ix < AMOUNTX; ix++) {
        for (let iy = 0; iy < AMOUNTY; iy++) {
            //
            particle = particles[i++] = new THREE.Particle(material)
            particle.position.x = ix * SEPARATION - (AMOUNTX * SEPARATION) / 2
            particle.position.z = iy * SEPARATION - (AMOUNTY * SEPARATION) / 2
            //
            scene.add(particle)
        }
    }

    //GPU
    renderer = new THREE.CanvasRenderer()
    renderer.setSize(window.innerWidth, window.innerHeight)

    //
    container = document.getElementById(id)
    container.appendChild(renderer.domElement)

    //
    // document.addEventListener( 'mousemove', onDocumentMouseMove, false );
    // document.addEventListener( 'touchstart', onDocumentTouchStart, false );
    // document.addEventListener( 'touchmove', onDocumentTouchMove, false );
    // window.addEventListener( 'resize', onWindowResize, false );

}

function onWindowResize() {
    // windowHalfX = window.innerWidth / 2;// windowHalfY = window.innerHeight / 2;
    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()
    renderer.setSize(window.innerWidth, window.innerHeight)
}

/* 
function onDocumentMouseMove( event ) {
    mouseX = event.clientX - windowHalfX;
    mouseY = event.clientY - windowHalfY;

}

function onDocumentTouchStart( event ) {
    if ( event.touches.length === 1 ) {
        event.preventDefault();
        mouseX = event.touches[ 0 ].pageX - windowHalfX;
        mouseY = event.touches[ 0 ].pageY - windowHalfY;
    }
}

function onDocumentTouchMove( event ) {
    if ( event.touches.length === 1 ) {
        event.preventDefault();
        mouseX = event.touches[ 0 ].pageX - windowHalfX;
        mouseY = event.touches[ 0 ].pageY - windowHalfY;
    }
}*/

// 
function animate() {
    requestAnimationFrame(animate)
    render()
}
// 
function render() {
    camera.position.x += (mouseX - camera.position.x) * 0.05
    camera.position.y += (-mouseY - camera.position.y) * 0.05
    camera.lookAt(scene.position)
    let i = 0
    for (let ix = 0; ix < AMOUNTX; ix++) {
        for (let iy = 0; iy < AMOUNTY; iy++) {
            particle = particles[i++]
            particle.position.y =
                Math.sin((ix + count) * 0.3) * 50 + Math.sin((iy + count) * 0.5) * 50
            particle.scale.x = particle.scale.y =
                (Math.sin((ix + count) * 0.3) + 1) * 2 +
                (Math.sin((iy + count) * 0.5) + 1) * 2
        }
    }
    renderer.render(scene, camera)
    count += 0.06
}
// 
export default {
    initWave,
    animate,
    onWindowResize
}



